// src/components/Banner.js
import React from "react"
import { Link } from "gatsby"
import { makeStyles, useTheme } from "@material-ui/core/styles"
import Button from '@material-ui/core/Button'
import Grid from '@material-ui/core/Grid'
import Box from '@material-ui/core/Box'
import Typography from '@material-ui/core/Typography'
import useMediaQuery from '@material-ui/core/useMediaQuery'

const useStyles = makeStyles((theme) => ({
  banner: {
    backgroundColor: '#FF5733',
    color: '#fff',
    padding: theme.spacing(1),
    marginBottom: theme.spacing(2),
    height: '50px',
  },
  text: {
    paddingLeft: theme.spacing(2),
    [theme.breakpoints.up('sm')]: {
      paddingLeft: theme.spacing(7),
    },
  },
  button: {
    paddingRight: theme.spacing(2),
  },
  enterButton: {
    backgroundColor: "#555",
    color: '#FFFFFF',
    '&:hover': {
      backgroundColor: '#000000',
      textDecoration: 'underline',
    },
  },
}));

const Banner = () => {
  const classes = useStyles();
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('xs'));

  return (
    <Box className={classes.banner}>
      <Grid container justify={isMobile ? "flex-start" : "center"} alignItems="center">
        <Grid item xs={8} sm={6} className={classes.text}>
          <Typography variant={isMobile ? "subtitle2" : "h6"}>Join Our Writing Contest Now!</Typography>
        </Grid>
        <Grid item xs={4} sm={2} className={classes.button}>
          <Button component={Link} to="contest" color="inherit" className={classes.enterButton}>Enter</Button>
        </Grid>
      </Grid>
    </Box>
  )
}

export default Banner
