import React, { useState } from "react";
import { Button, Typography, IconButton, Box, makeStyles } from "@material-ui/core";
import CloseIcon from '@material-ui/icons/Close';
import EmailIcon from '@material-ui/icons/Email';
import axios from "axios";
import { InputBase } from '@mui/material'

const useStyles = makeStyles((theme) => ({
  wrapper: {
    height: 'auto',
    position: 'fixed',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    zIndex: theme.zIndex.tooltip + 1,
    backgroundColor: '#fff',
    borderRadius: '1rem',
    padding: '2rem 1rem',
    boxShadow: '1px 1px 2rem rgba(0,0,0,.3)',
    textAlign: 'center',
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500],
  },
  icon: {
    color: '#8B0000', // Icon color set to red
    fontSize: '2rem',
    padding: '1rem',
    borderRadius: '100%',
    margin: '0 0 1rem 0',
  },

  submitButton: {
    marginLeft: theme.spacing(1),
    backgroundColor: '#2f6697',
    color: 'white',
    padding: '2px 10px',
    margin: '5px',
    textTransform: 'none',
    transition: 'all .3s ease',
    '&:hover': {
      backgroundColor: '#2f6697',
    },
    '& span': {
      textDecoration: 'none',
      '&:hover': {
        textDecoration: 'underline',
      },
    },
  },




}));

const SubscribeForm = () => {
  const classes = useStyles();
  const [email, setEmail] = useState("");
  const [error, setError] = useState(null);
  const [open, setOpen] = useState(true);

  const validateEmail = (email) => {
    const re = /\S+@\S+\.\S+/;
    return re.test(email);
  };

  const handleInputChange = (e) => {
    setEmail(e.target.value);
    if (!validateEmail(e.target.value)) {
      setError('Not a valid email');
    } else {
      setError(null);
    }
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    const url = ""; // Your API URL here
    const data = { email };
    try {
      await axios.post(url, data);
      setError(null);
      alert("Subscribed successfully");
    } catch (error) {
      setError("An error occurred while subscribing");
    }
  };

  if (!open) return null;

  return (
    <div className={classes.wrapper}>
      <IconButton aria-label="close" className={classes.closeButton} onClick={() => setOpen(false)}>
        <CloseIcon />
      </IconButton>
      <EmailIcon className={classes.icon} />
      <Typography variant="h4" color="textPrimary">Subscribe</Typography>
      <Typography variant="subtitle1" color="textPrimary">Subscribe to our newsletter and stay updated.</Typography>
      <Box sx={{ mt: 2 }}>
        
        <Box sx={{ display: 'flex', alignItems: 'center', mt: 1 }}>
          <form onSubmit={handleSubmit}>
          <InputBase
                  id="newsletter-email"
                  value={email}
                  onChange={handleInputChange}
                  placeholder="Email"
                  sx={{ 
                    height: '30px', 
                    border: '1px solid #c4c4c4', 
                    borderRadius: '4px', 
                    pl: '10px', 
                    '&:focus': {
                      borderColor: 'red',
                      outline: 'none',
                      boxShadow: '0 0 0 0.2rem rgba(255, 0, 0, 0.25)', 
                    },
                    '&::placeholder': {
                      lineHeight: '30px',
                    },
                    '&.Mui-focused': {
                      borderColor: 'red',
                    },
                  }}
                />
            {error && (
              <Typography variant="body2" color="error.main">
                {error}
              </Typography>
            )}
                   <Button 
                      variant="contained" 
                      className={classes.submitButton}
                      onClick={handleSubmit}
                    >
                      Submit
                    </Button>
          </form>
        </Box>
      </Box>
    </div>
  );
};

export default SubscribeForm;
