import * as React from 'react'
import { Box, Container, Grid, Typography, Link, InputBase, Button } from '@mui/material'
import { Twitter, Instagram, Pinterest } from '@mui/icons-material'
import { Link as GatsbyLink } from 'gatsby'

const Footer = () => {

  const [email, setEmail] = React.useState('');
  const [error, setError] = React.useState(null);

  const MoreFromABCMenu = [
    { name: 'Competition', link: '/competition', isExternal: false },
    { name: 'Donate', link: '/donate', isExternal: false },
    { name: '', link: 'https://example.com/hindi', isExternal: true },
    { name: 'Weekly', link: '/weekly', isExternal: false }
  ];
  const ContactMenu = [
    { name: 'About', link: '/about', isExternal: false },
    { name: 'Contact Us', link: '/contactus', isExternal: false },
    { name: 'Help', link: '/help', isExternal: false },
    { name: 'Jobs', link: '/jobs', isExternal: false }
  ];
  

  const text = "This is some paragraph about the website. It explains the purpose of the website and any other relevant information.";

  const validateEmail = (email) => {
    const re = /\S+@\S+\.\S+/;
    return re.test(email);
  };

  const handleInputChange = (e) => {
    setEmail(e.target.value);
    if (!validateEmail(e.target.value)) {
      setError('Not a valid email');
    } else {
      setError(null);
    }
  };

  const handleButtonClick = () => {
    if (validateEmail(email)) {
      setEmail('');
      setError(null);
      // Handle the email submission here
    } else {
      setError('Please enter a valid email');
    }
  };

  return (
    <Box sx={{ backgroundColor: '#f5f5f5', p: 2 }}>
      <Container maxWidth="lg">
        <Grid container spacing={2}>
          <Grid item xs={12} md={6}>
            <Typography variant="h6" color="text.primary" gutterBottom>
              Site Name
            </Typography>
            <Typography variant="body2" color="text.secondary">
              {text}
            </Typography>
            <Box sx={{ mt: 2 }}>
              <Typography variant="body2" color="error.main">
                To get newsletter:
              </Typography>
              <Box sx={{ display: 'flex', alignItems: 'center', mt: 1 }}>
                <InputBase
                  id="newsletter-email"
                  value={email}
                  onChange={handleInputChange}
                  placeholder="Email"
                  sx={{ 
                    height: '30px', 
                    border: '1px solid #c4c4c4', 
                    borderRadius: '4px', 
                    pl: '10px', 
                    '&:focus': {
                      borderColor: 'red',
                      outline: 'none',
                      boxShadow: '0 0 0 0.2rem rgba(255, 0, 0, 0.25)', 
                    },
                    '&::placeholder': {
                      lineHeight: '30px',
                    },
                    '&.Mui-focused': {
                      borderColor: 'red',
                    },
                  }}
                />
                {error && (
                  <Typography variant="body2" color="error.main">
                    {error}
                  </Typography>
                )}
                <Button 
                  variant="contained" 
                  onClick={handleButtonClick}
                  sx={{ 
                    ml: 1,
                    backgroundColor: '#2f6697',
                    color: 'white',
                    padding: '2px 10px',
                    margin: '5px',
                    textTransform: 'none',
                    textDecoration: 'none',
                    transition: 'all .3s ease',
                    '&:hover': {
                      textDecoration: 'underline',
                      backgroundColor: '#2f6697',
                    }
                  }}
                >
                  Submit
                </Button>
              </Box>
            </Box>
          </Grid>
          <Grid item xs={6} md={3}>
            <Typography variant="h6" color="text.primary" gutterBottom>
              More from ABC
            </Typography>
            <Box component="nav">
            {MoreFromABCMenu.map((menuItem, index) => (
  menuItem.isExternal ? (
    <Link
      key={index}
      href={menuItem.link}
      target="_blank"
      rel="noopener noreferrer"
      style={{ textDecoration: 'none', color: 'inherit', cursor: 'pointer', display: 'block' }}
    >
      <Box display="inline-block">
        <Typography variant="body2" color="text.secondary" sx={{ '&:hover': { borderBottom: '1px solid red' } }}>{menuItem.name}</Typography>
      </Box>
    </Link>
  ) : (
    <GatsbyLink
      key={index}
      to={menuItem.link}
      style={{ textDecoration: 'none', color: 'inherit', cursor: 'pointer', display: 'block' }}
    >
      <Box display="inline-block">
        <Typography variant="body2" color="text.secondary" sx={{ '&:hover': { borderBottom: '1px solid red' } }}>{menuItem.name}</Typography>
      </Box>
    </GatsbyLink>
  )
))}
            </Box>
          </Grid>
          <Grid item xs={6} md={3}>
            <Typography variant="h6" color="text.primary" gutterBottom>
              Contact
            </Typography>
            <Box component="nav">
            {ContactMenu.map((menuItem, index) => (
  menuItem.isExternal ? (
    <Link
      key={index}
      href={menuItem.link}
      target="_blank"
      rel="noopener noreferrer"
      style={{ textDecoration: 'none', color: 'inherit', cursor: 'pointer', display: 'block' }}
    >
      <Box display="inline-block">
        <Typography variant="body2" color="text.secondary" sx={{ '&:hover': { borderBottom: '1px solid red' } }}>{menuItem.name}</Typography>
      </Box>
    </Link>
  ) : (
    <GatsbyLink
      key={index}
      to={menuItem.link}
      style={{ textDecoration: 'none', color: 'inherit', cursor: 'pointer', display: 'block' }}
    >
      <Box display="inline-block">
        <Typography variant="body2" color="text.secondary" sx={{ '&:hover': { borderBottom: '1px solid red' } }}>{menuItem.name}</Typography>
      </Box>
    </GatsbyLink>
  )
))}
            </Box>
          </Grid>
        </Grid>
        <Box sx={{ mt: 2 }}>
          <hr />
          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
            <Typography variant="body2" color="text.secondary">
               {new Date().getFullYear()} Site Name. All rights reserved.
            </Typography>
            <Box>
              <Link href="https://twitter.com/your-handle" target="_blank" rel="noopener noreferrer" underline="none">
                <Twitter sx={{ color: 'red', '&:hover': { color: 'darkred' } }} />
              </Link>
              <Link href="https://instagram.com/your-handle" target="_blank" rel="noopener noreferrer" underline="none">
                <Instagram sx={{ color: 'red', '&:hover': { color: 'darkred' } }} />
              </Link>
              <Link href="https://pinterest.com/your-handle" target="_blank" rel="noopener noreferrer" underline="none">
                <Pinterest sx={{ color: 'red', '&:hover': { color: 'darkred' } }} />
              </Link>
            </Box>
          </Box>
        </Box>
      </Container>
    </Box>
  )
}

export default Footer;
